export default {
	data() {
		return {
			date: new Date(),
			week: '',
			statusBarHeight:'',
			tabIndex: 0,
			scrollInto: "",
			listHeight: '',
			current: '',
			tabBars:[{
				name: '课程',
				id: 'class'
			},{
				name: '热点',
				id: 'hot',
			},{
				name: '家教',
				id: 'teacher'
			},
			{
				name: '学生',
				id: 'student'
			}]
		}
	},
	mounted() {
		this.getInit()
	},
	methods: {
		getInit(){
			let now = new Date();
			let day = now.getDay();
			let weeks = new Array("星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六");
			this.week = weeks[day]
			uni.getSystemInfo({
			    success: (res)=> {
			        this.statusBarHeight = res.statusBarHeight + 'px'
					const query = uni.createSelectorQuery().in(this);
					query.select('.scroll-h').boundingClientRect(data => {
					}).exec();
					
			    }
			});
		}
	},
	filters:{
		formatDate: (value)=> {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			return `${y}-${MM}-${d}`;
		}
	}
}
